/* ------------ Nunito types ----------- */
/* --------------------------------------*/

@font-face {
    font-family: 'Nunito_Sans';
    src:url(".././fonts/nunito_sans/NunitoSans-Light.ttf");
    font-weight: 300;
    font-style: normal;
} 


@font-face {
    font-family: 'Nunito_Sans';
    src:url(".././fonts/nunito_sans/NunitoSans-Regular.ttf");
    font-weight: 400;
    font-style: normal;
} 

@font-face {
    font-family: 'Nunito_Sans';
    src:url(".././fonts/nunito_sans/NunitoSans-Bold.ttf");
    font-weight: 600;
    font-style: normal;
} 

